@import '/src/common/styles/sass/variables';

@import '/src/common/styles/sass/mixins';

.back {
  cursor: pointer;

  display: flex;
  gap: 12px;
  align-items: center;

  margin-top: 24px;
  margin-left: 140px;

  .image {
    width: 14px;
    height: 9px;
  }

  .backText {

    @include text(14px, 400, 24px);

    font-style: normal;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  place-content: center;

  width: 413px;
  margin: 60px auto;

  background-color: #fff;
  -webkit-box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);

  .container {
    position: relative;

    display: flex;
    flex-direction: column;
    align-items: center;

    margin: 34px 0 38px 0;

    .heading {
      font-size: 26px;
      font-weight: 600;
      line-height: 32px;
    }

    .image {
      position: relative;

      width: 96px;
      height: 96px;
      margin: 30px 0 0;

      background: url('../../assets/images/person.png');
      background-size: cover;
      border-radius: 50%;

      .cameraIcon {
        cursor: pointer;

        position: absolute;
        top: 64px;
        left: 64px;

        width: 32px;
        height: 32px;

        background: url('../../assets/icons/camera.svg');
        background-size: cover;
        border: 1px solid #fff;
        border-radius: 50%;
      }
    }

    .email {
      padding: 30px 0 40px;

      font-size: 14px;
      font-weight: 400;
      line-height: 24px;
      text-align: center;

      opacity: 0.5;
    }

    .fieldError {

      @include text(14px, 600, 22px);

      position: absolute;
      top: 232px;

      width: 347px;

      color: $errorColor;
      text-align: center;
    }

    .button {
      cursor: pointer;

      position: relative;

      width: 127px;
      height: 36px;
      padding: 0 0 0 20px;

      font-size: 16px;

      background: $bgSecondColor;
      border: none;
      border-radius: 30px;
      box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);

      &:hover {
        background: $bgColor;
      }

      .logOutIcon {
        position: absolute;
        top: 10px;
        left: 16px;

        width: 16px;
        height: 16px;

        background: url('../../assets/icons/logout.svg');
      }
    }
  }
}
