.formWrapper {
  display: flex;
  flex-direction: column;
  place-content: center;

  width: 413px;
  margin: 60px auto;

  background-color: #fff;
  -webkit-box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);
}

.formContainer {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 34px 0 38px 0;
}

.heading {
  font-size: 26px;
  font-weight: 600;
  line-height: 32px;
}

.form {
  position: relative;

  display: flex;
  flex-direction: column;
  place-content: center;

  margin: 40px 34px;
}

.label {
  display: inline-block;

  margin-top: 24px;

  font-size: 13px;
  font-weight: 400;
  line-height: 20px;

  opacity: 0.5;
}

.instructions {
  padding: 26px 0 64px;

  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  text-align: center;

  opacity: 0.5;
}

.tryLogIn {
  text-decoration: none;
}

.tryLogIn:hover {
  text-decoration: underline;
}

.button {
  width: 347px;
  height: 36px;
}

.account {
  margin-bottom: 7px;

  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  color: #000;

  opacity: 0.5;
}

.formContainer a {
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: var(--primaryColor);
}
